import React, { useLayoutEffect, useRef, useState } from "react";
import { Carousel } from "react-bootstrap";
import { useSelector } from "react-redux";
import Switch, { Case } from "react-switch-case";
import * as h from "~/Helpers";

let player;

const Context = () => {
   const { module } = useSelector((e) => e.redux);
   const { listContent } = module;
   const videoRef = useRef(null);
   const youtubeRef = useRef(null);

   // array
   const [slideData, setSlideData] = useState([]);

   // string
   const [interval, setInterval] = useState(3000);

   useLayoutEffect(() => {
      if (h.arrLength(listContent)) {
         const _slide = ["images", "video", "youtube"];
         const _static = ["running_text", "pengumuman"];

         const slideData = [];
         const staticData = [];
         listContent.forEach((row) => {
            if (_slide.includes(h.parse("caption", row))) {
               slideData.push(row);
            }
            if (_static.includes(h.parse("caption", row))) {
               staticData.push(row);
            }
         });

         if (h.arrLength(slideData)) {
            setSlideData(slideData);
         }
      }
      return () => {};
   }, [listContent]);
   const getYoutubeVideoId = (url) => {
      const regExp = /^.*(?:youtu.be\/|v\/|e\/|u\/\w+\/|embed\/|v=)([^#\&\?]{11}).*/;
      const match = url.match(regExp);
      return match?.[1] ? match[1] : null;
   };

   const onPlayerReady = () => {
      if (player) {
         player.playVideo();
         setInterval(player.getDuration() * 1000);
      }
   };

   const onPlayerStateChange = (event) => {
      if (event.data == YT.PlayerState.PLAYING) {
         player.unMute();
      } else {
         console.log("Video dijeda atau berhenti");
      }
   };

   const onYouTubeIframeAPIReady = (ref, videoId) => {
      player = new YT.Player(ref, {
         height: "100%",
         width: "100%",
         videoId,
         playerVars: {
            mute: 1,
            controls: 0,
            autoPlay: 1,
         },
         events: {
            onReady: onPlayerReady,
            onStateChange: onPlayerStateChange,
         },
      });
   };

   let rowIndex = 0;

   const onSlidEnds = (index) => {
      const prevData = slideData[index - 1];
      const data = slideData[index];
      if (h.parse("caption", data) === "video" && videoRef.current) {
         const video = videoRef.current;

         const hours = Math.floor(video.duration / 3600);
         const minutes = Math.floor((video.duration % 3600) / 60);
         const seconds = Math.floor(video.duration % 60);

         setInterval(seconds * 1000 + minutes * 60_000 + hours * 3600 * 60_000);

         video.currentTime = 0;
         video.play();

         setTimeout(() => {
            video.muted = false;
         }, 2000);
      }

      if (h.parse("caption", prevData) === "video" && videoRef.current && videoRef.current.currentTime > 0) {
         videoRef.current.muted = true;
         videoRef.current.currentTime = 0;
         videoRef.current.pause();
      }

      if (h.parse("caption", data) === "youtube") {
         const video = youtubeRef.current;
         onYouTubeIframeAPIReady(video, getYoutubeVideoId(h.parse("value", data)));
      }

      const videoList = ["video", "youtube"];
      if (videoList.includes(h.parse("caption", data))) return;

      videoRef.current.muted = true;
      videoRef.current.currentTime = 0;
      videoRef.current.pause();
      player.stopVideo();
      setInterval(3000);
   };

   return (
      <Carousel onSlid={onSlidEnds} interval={interval} controls={false} indicators={false} fade={true}>
         {slideData.map((row) => {
            return (
               <Carousel.Item key={rowIndex++}>
                  <Switch condition={h.parse("caption", row)}>
                     <Case value="images">
                        <img src={`http://192.168.176.193:8002/getfile/images/${h.parse("value", row)}`} alt={h.parse("value", row)} />
                     </Case>
                     <Case value="video">
                        <video ref={videoRef} src={`http://192.168.176.193:8002/video/${h.parse("value", row)}`} muted />
                     </Case>
                     <Case value="youtube">
                        <div ref={youtubeRef} />
                     </Case>
                  </Switch>
               </Carousel.Item>
            );
         })}
      </Carousel>
   );
};
export default Context;
